.menubar {
    background-color: #3498db;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
  }
  
  .menu-item {
    position: relative;
  }
  
  .submenu {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #3498db;
    list-style-type: none;
    padding: 10px;
    margin-top: 1px; /* Adjusted margin */
  }
  
  .menu-item:hover .submenu {
    display: block; /* Changed from flex to block */
    flex-direction: column;
  }
  
  li {
    margin: 0 15px;
    color: #fff;
    cursor: pointer;
  }
  